<script>
import axios from "axios";

const url = "https://api-dev.chsli.org:8000/users";

export default {
  data: () => ({
    ref: {},
    menuDOB: false,
    menuEncounterDate: false,
    //    formState: null,
    dialog: false,
    snackbar: {
      show: false,
      color: "",
      message: ""
    }
  }),
  mounted() {
    //    this.$refs.form.reset();
    this.ref = this.initializeRef();
  },
  watch: {
    menuDOB(val) {
      val && setTimeout(() => (this.$refs.picker.activePicker = "YEAR"));
    }
  },
  methods: {
    saveDOB(date) {
      this.$refs.menuDOB.save(date);
    },
    cancelForm() {
      this.dialog = false;
      this.ref = this.initializeRef();
    },
    submitForm() {
      this.$validator.validate().then(async valid => {
        if (!valid) {
          this.$el
            .querySelector(
              '[name="' + this.$validator.errors.items[0].field + '"]'
            )
            .scrollIntoView();
          window.scrollBy(0, -120); // adjust scrolling higher
          return;
        }

        try {
          await axios.post(url, this.ref);

          this.snackbar.color = "green";
          this.snackbar.message =
            "Your referral has been successfully submitted.  Thank you.";
          this.snackbar.show = true;

          // this.$refs.form.reset();
          this.ref = this.initializeRef();
        } catch (e) {
          this.snackbar.color = "red";
          this.snackbar.message =
            "We're sorry, we're not able to send this information at the moment, please try again later.  " +
            e;
          this.snackbar.show = true;
        }
      });
    },
    initializeRef() {
      return {
        ptFirstName: "",
        ptMiddleInitial: "",
        ptLastName: "",
        ptAddress: "",
        ptCity: "",
        ptZip: "",
        ptPhone: "",
        ptDOB: "",
        ptGender: "",
        ptInsurance1: "",
        ptPolicy1: "",
        ptInsurance2: "",
        ptPolicy2: "",
        ptDiagnosis1: "",
        ptDiagnosis2: "",
        ptMedicalHistory: "",
        ptReferralType: "",
        drEncounterDate: "",
        drMedicalFindings: "",
        drFirstName: "",
        drLastName: "",
        drAddress: "",
        drCity: "",
        drZip: "",
        drPhone: "",
        drLicense: "",
        drNPI: ""
      };
    }
  }
};
</script>